angular.element(document).ready(function ($timeout) {


  // Full height of sidebar
  function fix_height() {
    var heightWithoutNavbar = angular.element("body > #wrapper").height() - 61;
    angular.element(".sidebar-panel").css("min-height", heightWithoutNavbar + "px");

    var navbarHeight = angular.element('nav.navbar-default').height();
    var wrapperHeight = angular.element('#page-wrapper').height();

    if(navbarHeight > wrapperHeight){
      angular.element('#page-wrapper').css("min-height", navbarHeight + "px");
    }

    if(navbarHeight < wrapperHeight){
      angular.element('#page-wrapper').css("min-height", angular.element(window).height()  + "px");
    }

    if (angular.element('body').hasClass('fixed-nav')) {
      if (navbarHeight > wrapperHeight) {
        angular.element('#page-wrapper').css("min-height", navbarHeight  + "px");
      } else {
        angular.element('#page-wrapper').css("min-height", angular.element(window).height() - 60 + "px");
      }
    }

  }

  angular.element(window).bind("load resize scroll", function() {
    if(!angular.element("body").hasClass('body-small')) {
      fix_height();
    }
  });

  // Move right sidebar top after scroll
  angular.element(window).scroll(function(){
    if (angular.element(window).scrollTop() > 0 && !angular.element('body').hasClass('fixed-nav') ) {
      angular.element('#right-sidebar').addClass('sidebar-top');
    } else {
      angular.element('#right-sidebar').removeClass('sidebar-top');
    }
  });

  $timeout(function(){
    fix_height();
  });

  // Minimalize menu when screen is less than 768px
  angular.element(window).bind("load resize", function() {
    if (angular.element(document).width() < 769) {
      angular.element('body').addClass('body-small')
    } else {
      angular.element('body').removeClass('body-small')
    }
  })

});

(function() {
  'use strict';

  angular
    .module('inspinia')
    .run(runBlock);

  /** @ngInject */
  function runBlock($rootScope, $state, aws) {

		$rootScope.$on("$stateChangeStart", function(event, toState, toParams, fromState, fromParams){
	    if (toState.authenticate && !aws.isLoggedIn()){
	      // User isnâ€™t authenticated
	      $state.transitionTo("login");
	      event.preventDefault(); 
	    }
	  });
	
   
  }

})();

(function() {
  'use strict';

  angular
    .module('inspinia')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
	  $locationProvider.html5Mode(true);
    $stateProvider
    	
    	.state('login', {
        url: "/login",
        templateUrl: "app/login/login.html",
        authenticate: false
      })
    
    
      .state('forgot_password', {
        url: "/forgot-password",
        templateUrl: "app/forgot_password/forgot_password.html",
        authenticate: false
      })
    
    
    	
      .state('dashboard', {
        abstract: true,
        url: "/dashboard",
        templateUrl: "app/components/common/content.html",
        authenticate: true
      })
      .state('dashboard.main', {
        url: "/main",
        templateUrl: "app/main/main.html",
        authenticate: true
      })
			
			.state('users', {
        abstract: true,
        url: "/users",
        templateUrl: "app/components/common/content.html",
        authenticate: true,
        resolve: { 
	        loadPlugin: function ($ocLazyLoad) {
						return $ocLazyLoad.load([
						  {
					      serie: true,
					      files: ['assets/scripts/dataTables/datatables.min.js','assets/styles/dataTables/datatables.min.css']
						  },
						  {
					      serie: true,
					      name: 'datatables',
					      files: ['assets/scripts/dataTables/angular-datatables.min.js']
						  },
						  {
					      serie: true,
					      name: 'datatables.buttons',
					      files: ['assets/scripts/dataTables/angular-datatables.buttons.min.js']
							}
						]);
					}
                
	      }
      })
      .state('users.list', {
        url: "/list",
        templateUrl: "app/users/users.html",
        data: { pageTitle: 'Orders' },
        authenticate: true
      })
      .state('users.add', {
        url: "/add",
        templateUrl: "app/users/users-add.html",
        data: { pageTitle: 'Orders' },
        authenticate: true
      })
      
      
      .state('orders', {
        abstract: true,
        url: "/orders",
        templateUrl: "app/components/common/content.html",
        authenticate: true,
        resolve: { 
	        loadPlugin: function ($ocLazyLoad) {
					return $ocLazyLoad.load([
					  {
				      serie: true,
				      files: ['assets/scripts/dataTables/datatables.min.js','assets/styles/dataTables/datatables.min.css']
					  },
					  {
				      serie: true,
				      name: 'datatables',
				      files: ['assets/scripts/dataTables/angular-datatables.min.js']
					  },
					  {
				      serie: true,
				      name: 'datatables.buttons',
				      files: ['assets/scripts/dataTables/angular-datatables.buttons.min.js']
					  }
					]);
					}
                
	      }
      })
      .state('orders.list', {
        url: "/list",
        templateUrl: "app/orders/orders.html",
        authenticate: true,
        data: { pageTitle: 'Orders' },
      })
      .state('orders.edit', {
        url: "/edit/:orderId",
        templateUrl: "app/orders/orders-edit.html",
        authenticate: false,
        resolve: {
	        idCheck: function($stateParams, $location){
						if ($stateParams.orderId === undefined || $stateParams.orderId == "")
							$location.path('orders/list');
    			},
	        loadPlugin: function ($ocLazyLoad) {
            return $ocLazyLoad.load([
              {
                name: 'datePicker',
                files: ['assets/styles/datapicker/angular-datapicker.css','assets/scripts/datapicker/angular-datepicker.js']
              }
            ])
	        }
	      }
      })
      
      
      .state('orders.add', {
        url: "/add",
        templateUrl: "app/orders/orders-add.html",
        authenticate: true,
        data: { pageTitle: 'Orders' },
      });
      
      
    $urlRouterProvider.otherwise('dashboard/main');
  }

})();

